.container {
    width: 390px;
    height: 470px;
    min-height: 130px;
    /* border: 1px solid #888; */
    margin-bottom: 20px;
    composes: cardBackground from '../../../../../CommonUI/colors.css';
    transition: 0.3s;
    padding-top: 78px;
}

.container:hover {
    background-color: white;
    box-shadow: rgba(15, 15, 15, 0.05) 0px 0px 0px 1px,
    rgba(15, 15, 15, 0.1) 0px 5px 10px;
}

.iconSection {
    padding-top: 16px;
    padding-bottom: 8px;
    font-size: 28px;
    /* border: 1px solid #888; */
}

.projectName {
    font-size: 30px;
    composes: headingText from '../../../../../CommonUI/colors.css';
    font-weight: 700;
}

.desc {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 60px;
    font-size: 15px;
    composes: descText from '../../../../../CommonUI/colors.css';
}

.buttonSection {
    /* border: 1px solid #888; */
    width: 100%;
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    height: 40px;
    padding-top: 140px;
}

.buttonText {
    /* border-width: 2px; */
    width: 160px;
    height: 48px;
    /* border-style: solid; */
    composes: borderColor from '../../../../../CommonUI/colors.css';
    text-transform: capitalize;
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    border-radius: 28px;
    composes: backgroundColorButton from '../../../../../CommonUI/colors.css';
    color: white;
    cursor: pointer;
    transition: 0.3s;
}

.buttonText:hover {
    border: 2px solid #7730FF;
}

@media (max-width: 767px) {
    .container {
        width: 510px;
        height: 409px;
    }
}

@media (max-width: 550px) {
    .container {
        width: 330px;
        height: 440px;
    }
}