.container {
    width: 100%;
    min-height: 100%;
}

.headingSection {
    width: 100%;
    height: auto;
    padding: 30px 0;
    display: relative;
}

.laptopIcon {
    font-size: 45px;
}

.headingText {
    font-size: 45px;
    font-weight: 900;
    composes: headingText from '../../../../CommonUI/colors.css';
    word-spacing: 0px;
    line-height: 50px;
}

.descText {
    font-size: 15px;
    font-weight: 300;
    padding-top: 8px;
    padding-left: 10px;
    padding-right: 10px;
    composes: descText from '../../../../CommonUI/colors.css';
}

.projectListSection {
    width: 100%;
    height: auto;
    /* border: 1px solid #888888; */
    padding-top: 20px;
    /* padding-left: 24px;
    padding-right: 24px; */
    padding-bottom: 28px;
    display: flex;
    flex-direction: row;
    -webkit-flex-wrap: wrap;
    flex-wrap:         wrap;
    align-content: space-around;
    justify-content: space-around;
    align-items: center;
}

@media (max-width: 1120px) {
    .headingText{ 
        font-size: 35px;
    }
    .laptopIcon {
        font-size: 35px;
    }
    .descText {
        padding-top: 3px;
    }
}
@media (max-width: 457px) {
    .headingText{ 
        font-size: 25px;
    }
    .laptopIcon {
        font-size: 25px;
    }
    .descText {
        padding-top: 0;
    }
} 
